import React from 'react';
import useCountdownTimer from '../../hooks/useCountdownTimer'

import classes from './CountdownTimer.module.css'


const CountdownTimer = (props) => {
	const onTimerElapsed = () => { };

	const timer = useCountdownTimer({
		totalSeconds: (props.totalSeconds ?? 15),
		timerElapsed : onTimerElapsed
	});

	const formatTimeFragment = (time => { return time.toString().padStart(2, '0') });
	const formatTime = (time =>
		<React.Fragment>
			<label>{formatTimeFragment(time.hours)}</label>
			<label>:</label>
			<label>{formatTimeFragment(time.minutes)}</label>
			<label>:</label>
			<label>{formatTimeFragment(time.seconds)}</label>
		</React.Fragment>
	);

	const onStart = () => {
		timer.start();
	};

	const onStop = () => {
		timer.stop();
	};

	return (
		<div>
			<h1>Countdown Timer</h1>
			<hr />
			<div className={classes.timer}>
				{ formatTime(timer.timeLeft) }
			</div>
			<button type='button' className='btn' onClick={onStart}>Start</button>
			<button type='button' className='btn' onClick={onStop}>Stop</button>
		</div>
	);
};

export default CountdownTimer;